<template>
  <div class="payTransfer">
    <div class="header">
      <div style="float:left;margin-top: -5px;">
        <span style="font-size:16px;">当前位置:</span>
      </div>
      <div style="padding-left: 80px;">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>转账业务</el-breadcrumb-item>
          <el-breadcrumb-item>转账制单</el-breadcrumb-item>
          <el-breadcrumb-item>单笔付款</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    </div>
    <el-form ref="addForm" :model="addForm" :rules="rule" label-width="200px" class="content">
      <div v-if="stepNextBack === 1">
        <div class="pay-transfer-header mb-20">请选择付款人</div>

        <el-form-item label="付款人账号：" prop="payAccountNum">
          <div>
            <el-select v-model="addForm.payAccountNum" placeholder="全部" style="width:260px;">
              <el-option :label="addForm.payAccountNum" :value="addForm.payAccountNum"></el-option>
            </el-select>
          </div>
          <div style="margin-top: 10px;">
            <el-select v-model="addForm.payAccountSort" style="width:260px;">
              <el-option label="按账户优先级排序" value="sortAccount"></el-option>
            </el-select>
          </div>
          <div style="margin-top: 10px;">
            <el-select v-model="addForm.accountInfo" placeholder="请选择付款方账号" style="width:600px;">
              <el-option v-for="(item,index) of accountInfoList" :key="index" :label="item" :value="addForm.payAccountNum"></el-option>
            </el-select>
            <span style="line-height: 30px;padding-left: 15px;">
              <input type="checkbox" disabled /> 设置为默认付款账户
            </span>
          </div>
          <el-input type="text" placeholder="请输入模糊账号或模糊账号名称" style="width:260px;margin-top: 10px;"></el-input>
        </el-form-item>

        <div class="pay-transfer-header mb-20">请选择收款人</div>

        <el-form-item label=" 收款人行别：" prop="type">
          <el-radio-group v-model="addForm.type">
            <el-radio :label="'建行'">收款人为建行</el-radio>
            <el-radio :label="'他行'">收款人为他行</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="收款人账号：" prop="payeeAccountNum">
          <el-input type="text" readonly style="width:260px;" v-model="addForm.payeeAccountNum"></el-input>
          <span
            style="color: #0066b3;line-height: 30px;padding-left: 20px; cursor: pointer;"
            @click="oftenUseAccount"
          >常用收款账户</span>
        </el-form-item>

        <el-form-item label="收款方户名：" required v-if="addForm.type === '他行'">
          <el-input type="text" v-model="addForm.payeeAccountName" readonly style="width:260px;"></el-input>
        </el-form-item>
        <el-form-item prop="inputType" v-if="addForm.type === '他行'">
          <el-radio-group v-model="addForm.inputType">
            <el-radio :label="1">快速选择</el-radio>
            <el-radio :label="2" disabled>手动输入</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="开户行：" required v-if="addForm.type === '他行'">
          <div>
            <el-select v-model="addForm.bank" placeholder="请选择银行" style="width: 250px" @change="choseBank">
              <el-option v-for="item in bankList" :key="item.key" :label="item.value" :value="item.key"></el-option>
            </el-select>
            <el-input type="text" readonly style="width:220px;margin-left: 10px;"></el-input>
            <span style="margin-left: 10px;">模糊查询 在选择开户行之后，您可输入关键字或联行号查找具体网点</span>
          </div>
          <div style="margin-top: 10px;">
            <el-select v-model="addForm.province" placeholder="请选择省" @change="choseCity" style="width: 250px">
              <el-option v-for="item in provinceList" :key="item" :label="item" :value="item"></el-option>
            </el-select>
            <el-select v-model="addForm.city" placeholder="请选择市" style="width: 250px;margin-left: 10px;" @change="getSearBank">
              <el-option v-for="item in cityList" :key="item" :label="item" :value="item"></el-option>
            </el-select>
          </div>
          <div style="margin-top: 10px;">
            <el-select v-model="addForm.websitepoint" placeholder="请选择网点" style="width: 350px" @change="getSearBank">
              <el-option v-for="item in bankNames" :key="item" :label="item" :value="item"></el-option>
            </el-select>
          </div>
        </el-form-item>
        <div class="pay-transfer-header mb-20 mt-15">请填写交易金额及相关信息</div>
        <el-form-item label="定条件付款：">
          <input type="checkbox" disabled />
        </el-form-item>
        <el-form-item label="金额：" prop="money">
          <el-input v-model="addForm.money" style="width:260px;" @input="numToUpCase"></el-input>
          <span style="padding-left: 15px; line-height: 30px;">(元)</span>
          <span style="color: #0066cc;padding-left:15px; line-height: 30px;">交易限额查询</span>
        </el-form-item>
        <el-form-item label="大写金额：">
          <span>{{ upCase }}</span>
        </el-form-item>
        <el-form-item label="用途：" prop="toUse">
          <el-select v-model="addForm.toUse" style="width:260px;">
            <el-option v-for="item of purposeList" :label="item.purpose" :value="item.purpose" :key="item.purposeId"></el-option>
          </el-select>
          <span style="line-height: 30px;padding-left: 15px;">
            <input type="checkbox" disabled /> 设置为默认用途
          </span>
          <span
            style="color: #00a0e9;line-height: 30px;padding-left: 15px; cursor: pointer;"
            @click="useMaintain()"
          >用途维护</span>
        </el-form-item>
        <el-form-item label="客户方流水号：">
          <el-input v-model="addForm.customerNumber" style="width:260px;"></el-input>
        </el-form-item>
        <el-form-item label="定时定频设置：">
          <input type="checkbox" disabled />
        </el-form-item>
        <el-form-item>
          <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="nextStep">下一步</el-button>
        </el-form-item>
      </div>

      <div class="makingContent" v-if="stepNextBack === 2">
        <table style="border: 1px #62a7e3 solid;width:100%;margin-bottom: 20px;border-spacing: unset">
          <tbody>
            <tr>
              <td rowspan="3" width="80" class="rightBottomLine" style="background-color:#f7fbfe;">
                <p style="width: 20px;padding-left: 30px;font-weight: bold;margin: 0px;">付款人</p>
              </td>
              <td
                class="rightBottomLine"
                width="120"
                style="text-align: right;padding-right: 15px;background-color: #f7fbfe;"
              >户名：</td>
              <td
                class="rightBottomLine"
                width="340"
                style="text-align: left;padding-left: 15px;"
              >{{ companyInfo.name }}</td>
              <td rowspan="3" width="80" class="rightBottomLine" style="background-color: #f7fbfe;">
                <p style="width: 20px;padding-left: 30px;font-weight: bold;margin: 0px;">收款人</p>
              </td>
              <td
                class="rightBottomLine"
                width="120"
                style="text-align: right;padding-right: 15px;background-color: #f7fbfe;"
              >户名：</td>
              <td class="BottomLine" style="text-align: left;padding-left: 15px;">{{ addForm.payeeAccountName }}</td>
            </tr>
            <tr>
              <td
                class="rightBottomLine"
                style="background-color: #f7fbfe;text-align: right;padding-right: 15px;"
              >账号：</td>
              <td
                class="rightBottomLine"
                style="text-align: left;padding-left: 15px;"
              >{{ companyInfo.number }}</td>
              <td
                class="rightBottomLine"
                style="background-color: #f7fbfe;text-align: right;padding-right: 15px;"
              >账号：</td>
              <td class="BottomLine" style="text-align: left;padding-left: 15px;">{{ addForm.payeeAccountNum }}</td>
            </tr>
            <tr>
              <td
                class="rightBottomLine"
                style="background-color: #f7fbfe;text-align: right;padding-right: 15px;"
              >开户行：</td>
              <td
                class="rightBottomLine"
                style="text-align: left;padding-left: 15px;"
              >{{ companyInfo.bank }}</td>
              <td
                class="rightBottomLine"
                style="background-color: #f7fbfe;text-align: right;padding-right: 15px;"
              >开户行：</td>
              <td class="BottomLine" style="text-align: left;padding-left: 15px;">{{ addForm.payeeBank }}</td>
            </tr>
            <tr class="BottomLine">
              <td style="background-color: #f7fbfe;"></td>
              <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">金额（大写）：</td>
              <td style="text-align: left;padding-left: 15px;">{{ addForm.upCase }}</td>
              <td style="background-color: #f7fbfe;"></td>
              <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;">金额（小写）：</td>
              <td style="text-align: left;padding-left: 15px;">{{ addForm.money }}(元)</td>
            </tr>
            <tr>
              <td style="background-color: #f7fbfe;border-bottom: none"></td>
              <td style="background-color: #f7fbfe;text-align: right;padding-right: 15px;border-bottom: none">用途：</td>
              <td style="text-align: left;padding-left: 15px;border-bottom: none">
                <span>{{ addForm.toUse }}</span>
              </td>
              <td style="border-bottom: none"></td>
              <td style="border-bottom: none"></td>
              <td style="border-bottom: none"></td>
            </tr>
          </tbody>
        </table>

        <el-form-item label="跟单信息：">
          <el-input v-model="addForm.billInfo" style="width:260px;"></el-input>
        </el-form-item>
        <el-form-item label="选择下级复核员：">
          <el-select v-model="addForm.checkMan" style="width:260px;">
            <el-option label="主管" value="主管"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="短信通知下级复核员：">
          <input type="checkbox" disabled />
        </el-form-item>
        <el-form-item label="邮件通知下级复核员：">
          <input type="checkbox" disabled />
        </el-form-item>
        <div class="pay-transfer-header mb-20 mt-15">请输入认证信息</div>
        <el-form-item label="交易密码：" prop="pwd">
            <el-input v-model="addForm.pwd" style="width:260px;" type="password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="backStep">上一步</el-button>
          <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;margin-left: 135px;" @click="saveInfo">确认</el-button>
        </el-form-item>
      </div>
    </el-form>

    <el-form v-if="stepNextBack === 3">
      <el-form-item>
        <img src="../../assets/zhidanover.png" alt="">
      </el-form-item>
      <el-form-item style="text-align: center;">
        <el-button size="mini" type="primary" style="background-color: #1f93d5;border-color: #1f93d5;" @click="backStep">继续制单</el-button>
        <el-button size="mini" type="info" disabled>保存为常用收款人</el-button>
      </el-form-item>
    </el-form>

    <el-dialog
      v-el-drag-dialog
      title="中国建设银行 单位网上银行"
      width="1150px"
      :modal="false"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :visible.sync="dialogVisible">
      <div class="pay-transfer-header mb-20 mt-15">常用收款账号</div>
      <div style="padding: 10px;">
        关键词:<el-input v-model="keyword" style="width:260px;margin-left: 10px;"></el-input>
        <el-button type="primary" size="mini" @click="searchData" style="margin-left: 20px;">查询</el-button>
      </div>

      <el-table :data="accountData" border highlight-current-row fit size="mini" @selection-change="chosePaper">
        <el-table-column type="selection" align="center" />
        <el-table-column label="账号" prop="account" align="center"></el-table-column>
        <el-table-column label="户名" prop="accountName" align="center"></el-table-column>
        <el-table-column label="行别" prop="payName" align="center"></el-table-column>
        <el-table-column label="开户行" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.payName === '建行'">{{ scope.row.opneBankName }}</span>
            <span v-if="scope.row.payName === '他行'">{{ scope.row.websitepoint }}</span>
          </template>
        </el-table-column>
        <el-table-column label="账户别名" prop="ownSet" align="center"></el-table-column>
        <el-table-column label="优先级" prop="levalSet" align="center"></el-table-column>
        <el-table-column label="账户类别" prop="accountType" align="center"></el-table-column>
      </el-table>

      <div style="text-align: center;margin: 10px 0">
        <el-button type="primary" size="mini" @click="selectAccount">确定</el-button>
        <el-button type="primary" size="mini" @click="dialogVisible = false">关闭</el-button>
        <el-button type="info" size="mini" disabled>常用收款账户维护</el-button>
      </div>
      <div>
        <img src="../../assets/tishi1.png" alt="">
      </div>
    </el-dialog>
  </div>
</template>

<script>
import elDragDialog from '@/directive/el-drag-dialog' // base on element-ui
import { getGroupNames, resetArray, numberToUpperCase, getDatetime, getProvince, getCity, getBankName } from '@/utils'
export default {
  name: 'PayTransfer',
  directives: { elDragDialog },
  data () {
    return {
      keyword: '',
      accountData: [],
      bankNames: [],
      provinceList: [],
      city: null,
      cityList: [],
      upCase: '',
      multiSelection: [],
      stepNextBack: 1,
      checked: false,
      accountInfoList: {},
      addForm: {
        lastAccount: '*******',
        sumarry: '自定义',
        bank: '',
        city: '',
        websitepoint: '',
        province: null,
        checkMan: '主管',
        pwd: '',
        billInfo: '',
        upCase: '',
        customerNumber: '',
        payAccountNum: null,
        accountInfo: null,
        payAccountFirst: 'all',
        payAccountSort: 'sortAccount',
        type: '建行',
        inputType: 1,
        toUse: '',
        payeeGroup: null,
        payeeAccountNum: null,
        payeeAccountName: null,
        payeeBank: null,
        money: null,
        purpose: null,
        pzNum: null,
        payType: 1
      },
      nextForm: {

      },
      rule: {
        toUse: [{ required: true, message: '请选择用途', trigger: 'change' }],
        type: [{ required: true, message: '请选择账户类型', trigger: 'change' }],
        payAccountNum: [{ required: true, message: '请选择付款方账号', trigger: 'change' }],
        payeeGroup: [{ required: true, message: '请选择收款方分组', trigger: 'change' }],
        payeeAccountNum: [{ required: true, message: '请选择收款方账号', trigger: 'change' }],
        money: [{ required: true, message: '请填写金额', trigger: 'blur' }],
        pwd: [{ required: true, message: '请输入密码', trigger: 'blur' }],
        purpose: [{ required: true, message: '请选择用途', trigger: 'change' }],
        payType: [{ required: true, message: '请选择支付方式', trigger: 'change' }]
      },
      dialogVisible: false,
      pwd: ''
    }
  },
  computed: {
    bankList () {
      return this.$store.state.bank
    },
    purposeList () {
      return this.$store.state.purposeList
    },
    userInfo () {
      return this.$store.state.userInfo
    },
    companyInfo () {
      return this.$store.state.companyInfo
    },
    payeeGroupList () {
      return this.$store.state.payeeGroupList
    },
    payeeList () {
      return this.$store.state.payeeInfo
    },
    detailData () {
      return this.$store.state.payeeInfo
    }
  },
  watch: {

  },
  mounted () {

  },
  created () {
    this.initData()
  },
  methods: {
    initAccountData () {
      this.accountData = []
      for (const one of this.detailData) {
        if (one.payName === this.addForm.type) {
          this.accountData.push(one)
        }
      }
    },
    searchData () {
      if (this.keyword) {
        const _arr = []
        for (const one of this.accountData) {
          if (one.account.indexOf(this.keyword) > -1 || one.accountName.indexOf(this.keyword) > -1 || one.payName.indexOf(this.keyword) > -1 || one.opneBankName.indexOf(this.keyword) > -1 || one.websitepoint.indexOf(this.keyword) > -1) {
            _arr.push(one)
          }
        }

        this.accountData = _arr
      } else {
        this.initAccountData()
      }
    },
    // 选择银行
    choseBank () {
      this.choseBankVisible = true
      this.$nextTick(() => {
        this.addForm.province = null
        this.addForm.city = null
        this.searchList = []
        this.total = 0
        this.provinceList = getProvince(this.addForm.bank)
      })
    },
    // 根据省获取市
    choseCity () {
      this.cityList = getCity(this.addForm.bank, this.addForm.province)
    },
    getSearBank () {
      this.bankNames = getBankName(this.addForm.bank, this.addForm.province, this.addForm.city)

      if (this.bankNames.length > 0) {
        this.addForm.websitepoint = this.bankNames[0]
      }
    },
    numToUpCase () {
      if (this.addForm.money !== '' && this.addForm.money !== null) {
        this.upCase = numberToUpperCase(this.addForm.money)
      } else {
        this.upCase = ''
      }
    },
    selectAccount () {
      if (this.multiSelection.length === 0) {
        this.$message.error('请选择账号')
      } else if (this.multiSelection.length > 1) {
        this.$message.error('一次只能选择一个账号')
      } else {
        this.addForm.payeeAccountNum = this.multiSelection[0].account
        this.addForm.payeeAccountName = this.multiSelection[0].accountName
        if (this.multiSelection[0].payName === '建行') {
          this.addForm.payeeBank = this.multiSelection[0].opneBankName
        } else {
          this.addForm.payeeBank = this.multiSelection[0].websitepoint
        }

        this.dialogVisible = false
      }
    },
    chosePaper (val) {
      this.multiSelection = val
    },
    initData () {
      this.addForm.payAccountNum = this.companyInfo.number
      this.accountInfo = this.companyInfo.number

      this.accountInfoList[this.companyInfo.number] = this.companyInfo.number + this.companyInfo.name + this.companyInfo.bank

      // 分组列表
      resetArray()
      this.payeeGroupArrayList = getGroupNames(this.payeeGroupList)
    },
    oftenUseAccount () {
      this.initAccountData()
      this.dialogVisible = true
    },
    useMaintain () {
      this.$store.commit('saveRouteInfo', 'serviceManage')
      this.$router.push('/main/serviceManage/pay-purpose')
    },
    nextStep () {
      this.addForm.upCase = this.upCase

      this.$refs.addForm.validate((valid) => {
        if (valid) {
          this.stepNextBack = 2
        }
      })
    },
    backStep () {
      this.stepNextBack = 1
    },
    saveInfo () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          if (this.addForm.pwd === this.$store.state.userInfo.password) {
            this.addForm.id = new Date().getTime()
            this.addForm.created_at = getDatetime()
            let _t = getDatetime().substring(0, 10).replace('-', '').replace('-', '') + ''

            _t = this.changeStr(_t, 2, '*')
            _t = this.changeStr(_t, 3, '*')
            this.addForm.jzTime = _t
            this.addForm.jfMoney = this.addForm.money
            this.addForm.dfMoney = '0.00'
            const data = { ...this.addForm }
            this.$store.commit('savePayTransferInfo', data)
            this.saveHisInfo()
            this.$message.success('制单成功')
            this.stepNextBack = 3
          } else {
            this.$message.error('输入密码错误')
          }
        }
      })
    },
    saveHisInfo () { // 保存历史信息
      let _t = getDatetime().substring(0, 10).replace('-', '').replace('-', '') + ''

      _t = this.changeStr(_t, 2, '*')
      _t = this.changeStr(_t, 3, '*')
      const _v = {
        id: new Date(),
        time: _t,
        moneyType: '人民币元',
        moneyDw: '钞',
        latMoney: '*******',
        useMoney: '*******',
        state: '正常'
      }
      const data = { ..._v }
      this.$store.commit('saveHistoryInfo', data)
    },
    changeStr (str, index, changeStr) {
      return str.substring(0, index) + changeStr + str.substring(index + changeStr.length)
    }
  }
}
</script>

<style lang="scss" scoped>
.payTransfer {
  /deep/ .el-input__inner {
    padding: 0 5px;
  }
  .w-400 {
    width: 400px;
  }
  .header {
    padding: 20px 10px;
    // display: flex;
    // justify-content: space-between;
    font-size: 14px;
     /deep/ .el-breadcrumb__inner{
      font-size: 16px;
    }
  }
  .pay-transfer-header {
    background-color: #f8f8f8;
    padding: 5px 10px;
  }
  .payNumber {
    position: absolute;
    top: 62px;
    left: 125px;
    width: 300px;
    height: 25px;
    line-height: 25px;
  }
  .payeeNumber {
    position: absolute;
    top: 142px;
    left: 145px;
    width: 180px;
    height: 25px;
    line-height: 25px;
  }
  .payeeBank {
    position: absolute;
    top: 142px;
    left: 420px;
    width: 300px;
    height: 25px;
    line-height: 25px;
  }
  .payeeName {
    position: absolute;
    top: 182px;
    left: 145px;
    width: 300px;
    height: 25px;
    line-height: 25px;
  }
  .payeeMoney {
    position: absolute;
    top: 263px;
    left: 105px;
    width: 100px;
    height: 25px;
    line-height: 25px;
  }
  .payeePurpose {
    position: absolute;
    top: 263px;
    left: 255px;
    width: 100px;
    height: 25px;
    line-height: 25px;
  }
  .payeeCharge {
    position: absolute;
    top: 263px;
